.list-item__upload, .list-item__upload_btn {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center; }

.list-item__upload {
  height: 72px;
  border-bottom: 1px solid #d4d4d4; }
  .list-item__upload_btn {
    -webkit-border-radius: 4px;
            border-radius: 4px;
    border: 1px dotted #aaaaaa;
    background-color: #ffffff;
    margin: 16px;
    height: 32px;
    cursor: pointer; }

.upload-img__logo {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  opacity: 0.25; }

.upload-text__title {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.71;
  color: #d4d4d4; }

.upload-img__add {
  width: 16px;
  height: 16px;
  margin-right: 8px; }

.upload-text__add {
  font-size: 12px;
  font-weight: 600;
  line-height: 1.33;
  letter-spacing: 0.3px;
  color: #414141; }

.list-item__dropzone {
  height: 136px;
  width: 100%;
  position: absolute;
  bottom: 0;
  cursor: pointer; }
  .list-item__dropzone > * {
    display: none; }
  .list-item__dropzone:hover ~ .list-item__upload {
    background: #f6fff9; }
  .list-item__dropzone:hover ~ .list-item__upload_btn {
    border: 1px dotted #ff9000; }
